/** CSS reset */

* {
  box-sizing: border-box;
}

html,
body,
div,
nav,
span,
textarea,
button,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  font: inherit;
  font-family: 'Noto Sans KR', sans-serif;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
h5 {
  color: grey;
}

/** landpage CSS */
.landpage-container {
  max-width: 100%;
  width: 100vw;
  height: 100vh;
  background-size: cover;

  background-image: url(https://images.velog.io/images/seonja/post/f8159754-11b9-42cc-8c89-be8401a33ed7/background-min.png);
}
.nav-container {
  height: 64px;
  background-color: rgba(245, 245, 245, 0.4);
  display: flex;
  justify-content: flex-end;
  padding-right: 48px;
}
.nav-item {
  font-size: 20px;
  padding: 20px;
  margin: auto 0;
  font-weight: 700;
  color: #0074e8;
  cursor: pointer;
}
.main-quote {
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin: 15%;
}

.register-container {
  max-width: 40vw;
  min-width: 300px;
  margin: auto;
}

@media (max-width: 960px) {
  .landpage-container {
    background-image: url(https://images.velog.io/images/seonja/post/27e68467-006d-4c57-af87-30b35e569a29/background-min.png);
  }
}

@media (max-width: 500px) {
  .landpage-container {
    background-image: url(https://images.velog.io/images/seonja/post/8644551d-c825-412b-81ce-1332e0915f9e/background_small-min.png);
  }
}
